<template>
<nav class="fixed-bottom navbar-light  bottom-h">
  <img src="@/assets/images/RevokeRight.png" onclick="Blockly.mainWorkspace.undo(1)"  class="float-right revoke" height="30" alt="撤销" srcset="">
  <img src="@/assets/images/RevokeLeft.png" onclick="Blockly.mainWorkspace.undo(0)" class="float-right revoke" height="30" alt="恢复" srcset="">
  <button type="button" class="btn btn-warning float-right" @click="$emit('openCode')" >代码<i class="el-icon-caret-top"></i></button>
  <button type="button" class="btn btn-warning float-right" onclick='openWin()'>串口监视器</button>
   <button type="button" class="btn btn-warning float-right" onclick="openFile()">打开</button>
    <button type="button" class="btn btn-warning float-right" onclick="discard()">新建</button>
  <button type="button" class="btn btn-warning float-right" onclick="saveXml()">保存</button>
  <!--<button type="button" class="btn btn-warning float-right" onclick="save()">保存</button>-->
  <button type="button" class="btn btn-warning float-right" @click='$emit("openPort",1)'>下载</button>
  <button type="button" class="btn btn-warning float-right" @click='$emit("openPort",0)'>验证</button>
</nav>
</template>

<script>
export default {
  data() {
    return {
      fullscreenLoading: false
    };
  },
  mounted() {},
  methods: {
  }
};
</script>

<style scoped>
.float-right {
  margin: 7px;
}

.bottom-h {
  height: 52px;
  overflow: hidden;
  background-color: rgba(255, 247, 153, 1);
  padding-right: 35px;
}

.revoke {
  line-height: 52px;
}

.revoke:hover {
  opacity: 0.4;
}

img {
  cursor: pointer;
}
</style>



