import Vue from 'vue'
import Router from 'vue-router'
import container from '@/components/index'
import bigShot from '@/components/bigShot/index'
import Classroom from '@/components/Classroom/index'
import modular from '@/components/Classroom/modular'
import modularRight from '@/components/Classroom/modularRight'
import paradise from "@/components/Classroom/paradise"
import paradiseRight from "@/components/Classroom/paradiseRight"
import build from "@/components/Classroom/build"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      // redirect: "/bigShot",
      component: container
    },
    {
      path: '/bigShot',
      name: 'bigShot',
      component: bigShot
    },
    {
      path: '/classroom',
      name: 'Classroom',
      redirect:"/classroom/modular/0",
      component: Classroom,
      children:[
        {
          name: "modular",
          path:'/classroom/modular/:id',
          components: {
            default:modular,
            classright:modularRight
          }
        },
        {
            name: 'paradise',
            path: '/classroom/paradise',
            components:{
                default: paradise,
                classright: paradiseRight
          }
        }
      ]
    },
      {
          path: '/build',
          name: 'build',
          component: build
      }
  ]
})



